.tickets-page{
    padding: 10%;
    margin-top: -100px;
    @media (max-width: 1090px) {
        padding: 10% 20px;
      }
      @media (max-width: 790px) {
        padding:10% 20px;
      }
      .txt {
        padding: 20px 0;
        width: 70%;
        margin: 0 auto;
        text-align: center;
        @media (max-width: 790px) {
          width: 100%;
          text-align: center;
        }
    
        h1 {
          font-size: 3.7em;
          font-weight: bold;
          margin: 10px 0;
          color: white;
          @media (max-width: 990px) {
            font-size: 3.7em;
          }
          @media (max-width: 590px) {
            font-size: 2.5em;
          }
        }
        p {
          font-size: 1.1em;
          font-weight: 400;
          margin: 10px 0;
          color: white;
          @media (max-width: 990px) {
            font-size: 16px;
          }
        }
      }
      .form{
        @media (max-width:790px) {
            flex-direction: column-reverse;
        }
        .left{
            width: 45%;
            @media (max-width:790px) {
               width: 100%;
            }
            img{
                width: 100%;
            }
            .form-control{
                padding: 20px;
                text-align: left;
                align-items: start;
                background-color: rgba(255, 255, 255, 0.048);
                h3{
                    font-weight: 400;
                    text-align: left;
                }
                input,select{
                    width: 100%;
                    margin-bottom: 20px;
                    padding: 10px;
                    outline: none;
                }
                .btn-max{
                    width: 100%;
                }
            }
        }
      }
}