.feat-section {
  padding: 50px 8%;
  background-color: rgba(255, 255, 255, 0.048);

  @media (max-width: 990px) {
    padding: 10% 20px;
  }
  @media (max-width: 790px) {
    padding: 20px;
  }
  .txt {
    padding: 20px 0;
    width: 70%;
    margin: 0 auto;
    text-align: center;
    @media (max-width: 790px) {
      width: 100%;
      text-align: center;
    }

    h1 {
      font-size: 4.7em;
      font-weight: bold;
      margin: 10px 0;
      color: white;
      @media (max-width: 990px) {
        font-size: 3.7em;
      }
      @media (max-width: 590px) {
        font-size: 2.5em;
      }
    }
    p {
      font-size: 1.1em;
      font-weight: 400;
      margin: 10px 0;
      color: white;
      @media (max-width: 990px) {
        font-size: 16px;
      }
    }
  }
  .feat {
    @media (max-width: 590px) {
      flex-direction: column;
    }
    .left {
      padding: 10px;
      width: 45%;
      @media (max-width: 790px) {
        width: 100%;
        text-align: center;
        padding: 0;
      }
      img {
        width: 100%;
      }
      span {
        text-transform: uppercase;
        font-size: 2em;
        margin: 10px 0;
      }
    }
  }
}
